<template>
  <div class="mt-4">
    <v-card-title>
      <div class="text-h5 font-weight-light">
        Create Promotion
      </div>
    </v-card-title>
    <v-card class="pa-2 ma-2">
      <v-img :src="form.ImagesPath" width="100px"></v-img>
      <v-file-input
        @change="createBase64Image"
        color="deep-purple accent-4"
        counter
        accept="image/*"
        label="Upload Image"
        placeholder="Select your files"
        prepend-icon="mdi-paperclip"
        outlined
        :show-size="1000"
      >
        <template v-slot:selection="{ index, text }">
          <v-chip
            v-if="index < 2"
            color="deep-purple accent-4"
            dark
            label
            small
          >
            {{ text }}
          </v-chip>

          <span
            v-else-if="index === 2"
            class="text-overline grey--text text--darken-3 mx-2"
          >
            +{{ files.length - 2 }} File(s)
          </span>
        </template>
      </v-file-input>
      <v-select v-model="form.ProductID" :loading="products.loading" :items="products.data" item-text="FinancePurpose" item-value="FinancePurpose" label="Product"
        outlined></v-select>
      <v-text-field v-model="form.Promotion_Description" :loading="loading" label="Promotion Description"
        :error-messages="errors.FinancePurpose" outlined></v-text-field>
      <v-text-field v-model="form.RedirectTo" :loading="loading" label="RedirectTo"
        :error-messages="errors.FinancePurpose" outlined></v-text-field>
      <v-select v-model="form.isactive" :items="items" item-text="text" item-value="value" label="Status"
        outlined></v-select>
      <v-btn color="primary" class="mr-4" :loading="data.loading" @click="simpan(form)">
        S A V E
      </v-btn>
    </v-card>
    <v-dialog v-model="loading" hide-overlay persistent width="300">
      <v-card color="primary" dark>
        <v-card-text>
          Please stand by
          <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import store from '@/store'

export default {
  name: 'PromotionCreate',
  data: () => ({
    loading: false,
    errors: {},
    form: {
      ImagesPath: '',
      ProductID: '',
      Promotion_Description: '',
      RedirectTo: '',
      isactive: ''
    },
    items: [
      {
        text: 'Active', value: '1'
      },
      {
        text: 'No Active', value: '0'
      }
    ]
  }),
  mounted () {
    store.dispatch('product/reqData')
  },
  computed: {
    ...mapState('promotion', {
      data: (state) => state
    }),
    ...mapState('product', {
      products: (state) => state
    })
  },
  methods: {
    ...mapActions('promotion', [
      'simpan'
    ]),
    createBase64Image: function (FileObject) {
      const reader = new FileReader()
      reader.onload = (event) => {
        this.form.ImagesPath = event.target.result
      }
      reader.readAsDataURL(FileObject)
    }
  }
}
</script>
